// src/components/MainPage.js
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import NavBar from './NavBar';

import buffet from './img/buffet.jpg';
import laptop from './img/laptop.jpg';
import diet from './img/diet.jpg';
import steak from './img/steak.jpg';
import emptyPlate from './img/empty_plate.jpg';

import './css/main.css';

export default function MainPage() {
  const [userInfo, setUserInfo] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    const storedUser = localStorage.getItem('user');
    if (storedUser) {
      setUserInfo(JSON.parse(storedUser));
    }
  }, []);

  return (
    <>
      <NavBar />
      <div className="mainpage-wrapper">
        {/*   */}
        <div className="hero-image-wrapper">
          <img className="responsive-img hero-img-horizontal" src={buffet} alt=" " />
          <div className="main-text over-hero">
            <h1>, <span></span> ?</h1>
            <p>AI     </p>
            <button onClick={() => navigate('/meal-recommend')}>   </button>
          </div>
        </div>

        {/*    */}
        <section className="card-section">
          <div className="card-grid">

            {/*     */}
            <div className="card">
              <img src={diet} alt=" " className="card-img" />
              <div className="card-content">
                <h3>  </h3>
                <p>  <br />    !</p>
                <button onClick={() => navigate('/image-analysis')}></button>
              </div>
            </div>

            {/*     */}
            <div className="card">
              <img src={laptop} alt=" " className="card-img" />
              <div className="card-content">
                <h3>  </h3>
                <p>  <br />   .</p>
                <button onClick={() => navigate('/chatbot')}></button>
              </div>
            </div>

            {/* AI    */}
            <div className="card">
              <img src={emptyPlate} alt=" " className="card-img" />
              <div className="card-content">
                <h3>AI  </h3>
                <p>AI    <br /> .</p>
                <button onClick={() => navigate('/meal-plan')}>  </button>
              </div>
            </div>

          </div>
        </section>
      </div>
    </>
  );
}
