import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './css/main.css';

export default function NavBar() {
    const [menuOpen, setMenuOpen] = useState(false);
    const [userInfo, setUserInfo] = useState(null);
    const navigate = useNavigate();
    const BASE_URL = process.env.REACT_APP_BASE_URL;

    //  1. localStorage 
    useEffect(() => {
        const storedUser = localStorage.getItem('user');
        if (storedUser) {
            try {
                const parsed = JSON.parse(storedUser);
                setUserInfo(parsed);
                console.log(" NavBar  - localStorage ", parsed);
            } catch (err) {
                console.warn(" localStorage  ", err);
            }
        }
    }, []);

    //  2.    
    useEffect(() => {
        axios.get(`${BASE_URL}/users/ses`, { withCredentials: true })
            .then((res) => {
                if (res.data) {
                    console.log("   :", res.data);
                    setUserInfo(res.data);
                    localStorage.setItem('user', JSON.stringify(res.data));
                }
            })
            .catch(() => {
                console.log('  ');
                localStorage.removeItem('user');
                setUserInfo(null);
            });
    }, []);

    //  userInfo  
    useEffect(() => {
        console.log(' userInfo :', userInfo);
    }, [userInfo]);

    useEffect(() => {
        document.body.style.overflow = menuOpen ? 'hidden' : '';
    }, [menuOpen]);

    useEffect(() => {
        const handleResize = () => {
            if (window.innerWidth > 768 && menuOpen) {
                setMenuOpen(false);
            }
        };
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, [menuOpen]);

    //  
    const handleLogout = async () => {
        try {
            await axios.post(`${BASE_URL}/users/logout`, null, {
                withCredentials: true
            });

            localStorage.removeItem('user');
            setUserInfo(null);
            toast.success(' !');
            setMenuOpen(false);
            navigate('/');
        } catch (err) {
            console.error(' :', err);
            toast.error('   .');
        }
    };

    return (
        <>
            <nav className="glass-navbar">
                <div className="navbar-container">
                    <div className="navbar-left">
                        <Link to="/" className="logo"> <span>FoodyChat</span></Link>
                    </div>

                    <div className="navbar-center desktop-menu">
                        <Link to="/chatbot" className="nav-link"> <span></span></Link>
                        <Link to="/image-analysis" className="nav-link"> <span> </span></Link>
                        {userInfo && (
                            <Link to="/meal-plan" className="nav-link"> <span></span></Link>
                        )}
                        {userInfo && (
                            <Link to="/mypage" className="nav-link"> <span></span></Link>
                        )}
                        {userInfo && userInfo.membership_level?.toLowerCase() === 'admin' && (
                            <div className="dropdown">
                                <span className="nav-link dropdown-toggle"> <span></span></span>
                                <div className="dropdown-menu">
                                <Link to="/users/admin" className="dropdown-item"> </Link>
                                <Link to="/users/admin/logs" className="dropdown-item"> </Link>
                                <Link to="/users/admin/codes" className="dropdown-item"> </Link>
                                </div>
                            </div>
                        )}
                    </div>

                    <div className="navbar-right desktop-menu">
                        {userInfo ? (
                            <span onClick={handleLogout} className="nav-link logout-link"></span>
                        ) : (
                            <>
                                <Link to="/login" className="nav-button"></Link>
                                <Link to="/signup" className="nav-button signup"></Link>
                            </>
                        )}
                    </div>

                    <div
                        className={`hamburger ${menuOpen ? 'open' : ''}`}
                        onClick={() => setMenuOpen(!menuOpen)}
                        aria-label="Menu toggle"
                    >
                        <span />
                        <span />
                        <span />
                    </div>
                </div>
            </nav>

            <div className={`mobile-menu ${menuOpen ? 'show' : ''}`}>
                <Link to="/chatbot" onClick={() => setMenuOpen(false)}> </Link>
                <Link to="/image-analysis" onClick={() => setMenuOpen(false)}>  </Link>
                <Link to="/mypage" onClick={() => setMenuOpen(false)}> </Link>
                {userInfo && userInfo.membership_level?.toLowerCase() === 'admin' && (
                    <Link to="/users/admin" onClick={() => setMenuOpen(false)}> </Link>
                )}
                {userInfo ? (
                    <button onClick={() => { setMenuOpen(false); handleLogout(); }} className="mobile-logout-button">
                        
                    </button>
                ) : (
                    <>
                        <Link to="/login" onClick={() => setMenuOpen(false)}></Link>
                        <Link to="/signup" onClick={() => setMenuOpen(false)}></Link>
                    </>
                )}
            </div>
        </>
    );
}
